export default {
	data() {
		return {
			id:0,
			swipers:[]
		}
	},
	onLoad(options) {
		this.id=options.id
		this.getSwipers()
	},
	methods: {
		async getSwipers(){
			const res = await this.$myRequest({
				url:"/api/getthumimages/"+this.id
			})
			this.swipers=res.data.message;
		}
	}
}
